.page-header {
    position: fixed;
    top:0;
    left:0;
    width:100%;
    z-index: 1000;
    height: 90px;
    transition: 0.5s;
    &:hover{
        background: rgba(white, 0.1);
        box-shadow: 0 3px 4px rgba($color-main, 0.2);
    }
}
.page-header-fixed {
    background: rgba($color-second, 0.85);
    height: 80px;
    box-shadow: 0 2px 4px rgba($color-main, 0.4);
    .page-nav-text,
    .page-nav-list a { color: $color-main;}

    &:hover{
        background: $color-second
    }
}


.page-header .container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.page-logo {}
.page-logo img {
    max-width:125px;
    height: auto;
}

.page-nav {}
.page-nav-list {
    display: flex;
    list-style: none;
    padding:0;
    margin:0;
}

.page-nav-text,
.page-nav-list a {
    padding:15px 20px;
    display: block;
    font-size:19px;
    font-weight:400;
    color: rgba(white, 0.8);
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.5s;
    position: relative;
}
.page-nav-list a::after {
    content: "";
    position: absolute;
    bottom:0;
    height:2px;
    background: $color-main;
    left:0;
    right:0;
    transform: scaleX(0);
    transition: 0.5s;
}
.page-nav-list a:hover {
    color: $color-main;
}
.page-nav-list a:hover::after {
    transform: scaleX(1);
}

@media (max-width: 760px) {
	.page-header {
        position: absolute;
        padding-top:30px;
        &:hover{
            background: none;
            box-shadow:none;
        }

	}
	.page-header.sticky{
		background: transparent;
		box-shadow: none;
	}
	.page-header .container {
        flex-direction: column;
        text-align: center;
    }
    .page-logo {
        margin-bottom:30px;
    }
    .page-nav-list {
        flex-wrap: wrap;
    }
    .page-nav-list li {
        width:50%;
        border:1px solid rgba(#fff, 0.2);
        min-width:150px;


    }


    .page-nav-list li:hover {
        background: rgba(#fff, 0.1)
    }
}